@charset "UTF-8";
/*重置*/
*,
:before,
:after {
  box-sizing: border-box;
}
body {
  font-family: "Microsoft Yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
body,
ul,
dl,
dd {
  margin: 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
/*公用class*/
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.inline-block {
  display: inline-block;
  vertical-align: middle;
}
.pointer {
  cursor: pointer;
}
/* select */
.lui-select {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  height: 27px;
  border-radius: 4px;
  outline: none;
  background-color: white;
  font-size: 14px;
  line-height: 1.5em;
  user-select: none;
  cursor: default;
}
.lui-select:focus {
  border-color: #799bd2;
  box-shadow: 0 0 2px #0050c8;
}
.lui-select input {
  display: none;
}
.lui-select-btn {
  position: relative;
  height: 100%;
  display: block;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 10px;
  padding-right: 20px;
  border: solid 1px #999;
  border-radius: 4px;
}
.lui-select-btn:after {
  content: "";
  position: absolute;
  right: 5px;
  top: 50%;
  width: 0;
  height: 0;
  margin-top: -2px;
  border-top: 4px solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}
.lui-select-btn:hover {
  border-color: #666;
}
.lui-select:focus .lui-select-btn {
  border-color: #799bd2;
}
.lui-select-option {
  position: relative;
  z-index: 9;
  overflow: hidden;
  height: 0;
  border: solid 1px #999;
  border-top: none;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: white;
}
.lui-select-option li {
  padding-left: 10px;
  padding-right: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.lui-select-option li:hover {
  color: white;
  background-color: #47a4f2;
}
.lui-select-option li[disabled] {
  color: #999;
  background-color: white;
}
.lui-select.open {
  overflow: visible;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.lui-select.open .lui-select-btn {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.lui-select.open .lui-select-option {
  height: auto;
}
